import React from 'react';
import PropTypes from 'prop-types';
import Link from 'gatsby-link';

const Header = ({ siteTitle }) => (
  <div
    style={{
      marginBottom: '1.45rem',
    }}
  >
    <nav className="navbar is-fixed-top is-dark">
      <div className="container">
        <div className="navbar-brand">
          <Link to="/" className="navbar-item">
            {siteTitle}
          </Link>
          <div className="navbar-burger burger" data-target="navbarFixedTop">
            <span />
            <span />
            <span />
          </div>
        </div>

        <div id="navbarFixedTop" className="navbar-menu">
          <div className="navbar-start">
            <Link className="navbar-item" to="/projects/">
              Projects
            </Link>
            <Link className="navbar-item" to="/tech-stacks/">
              Technology Stacks
            </Link>
          </div>

          <div className="navbar-end">
            <div className="navbar-item">
              <div className="field is-grouped">
                <p className="control">
                  <a
                    className="bd-tw-button button"
                    target="_blank"
                    rel="noopener noreferrence"
                    href="https://twitter.com/shadez95"
                  >
                    <span className="icon">
                      <i className="fab fa-twitter" />
                    </span>
                    <span>Twitter</span>
                  </a>
                </p>
                <p className="control">
                  <a
                    className="bd-tw-button button"
                    target="_blank"
                    rel="noopener noreferrence"
                    href="https://github.com/shadez95"
                  >
                    <span className="icon">
                      <i className="fab fa-github" />
                    </span>
                    <span>Github</span>
                  </a>
                </p>
                <p className="control">
                  <a
                    className="bd-tw-button button"
                    target="_blank"
                    rel="noopener noreferrence"
                    href="mailto:contact@dixonbegay.com?body=%0D%0D%0D%0D%0DSent%20from%20dixonbegay.com"
                  >
                    <span className="icon">
                      <i className="fas fa-envelope" />
                    </span>
                    <span>Email</span>
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </nav>
  </div>
);

Header.propTypes = {
  siteTitle: PropTypes.string.isRequired,
};

export default Header;



